.text {
  font-size: 14px;
  line-height: 18px;
  color: $special_dark-grey;
}

.text--uppercase {
  text-transform: uppercase;
}

.text--form {
  font-family: $oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: $special_dark-grey;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}
